import React from 'react';
import 'whatwg-fetch';
import { Card, CardTitle, CardActions } from 'react-md/lib/Cards';
import StarRatingComponent from 'react-star-rating-component';
import Button from 'react-md/lib/Buttons';

export default class Sayings extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            user: {},
            totalVoted: 0
        };
    }


    /**
     *
     */
    componentWillMount = () => {
        this.getUser().then((fetchedUser) => {
            this.getSayings(fetchedUser)
        })
    };

    /**
     *
     * @returns {Promise.<TResult>}
     */
    getUser = () => {
        return fetch(`http://192.168.178.97:5000/api/user?name=${this.props.params.userName}`)
            .then(response => {
                return response.json();
            }).then(json => {
                this.setState({ user : json || {} });
                return json;
        });
    };

    /**
     *
     * @param user
     * @returns {Promise.<TResult>}
     */
    getSayings = (user) => {
        return fetch(`http://192.168.178.97:5000/api/sayings?userId=${user._id}`).then(response => {
            return response.json();
        }).then(json => {
            this.setState({data: json.sayings, totalVoted: json.totalVoted});
        });
    };

    /**
     *
     * @param rate
     * @param sayingId
     * @param index
     */
    addRating = (rate, sayingId, index) => {
        fetch("http://192.168.178.97:5000/api/rateSaying",
            {
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                method: "POST",
                body: JSON.stringify({sayingId, user: this.state.user._id, rate})
            })
            .then(res => {
                setTimeout(() => {
                    this.refs[`sayingContainer-${sayingId}`].className = 'hidden';
                }, 1000);
            })
            .then(() => {
                setTimeout(() => {
                    this.getSayings(this.state.user)
                }, 1500);

            })
            .catch(function(res){ console.log(res) })
    };


    /**
     *
     * @returns {XML}
     */
    render = () => {

        const jsx = this.state.data.map((sayingSchema, index ) => {
            return (
                <div ref={`sayingContainer-${sayingSchema._id}`} key={sayingSchema._id} style={{width: '100%'}}>
                    <Card className="card-display-mode">
                      <CardTitle title={sayingSchema.saying} />
                      <CardActions>
                          <StarRatingComponent
                              key={sayingSchema._id}
                              name={sayingSchema._id}
                              starCount={3}
                              value={0}
                              onStarClick={(value) => this.addRating(value, sayingSchema._id, index)}
                          />
                          <Button onClick={() => this.addRating(0, sayingSchema._id, index)} icon secondary>close</Button>
                      </CardActions>
                    </Card>
              </div>
          )
      });
      return (
      <div className="md-grid">
          <h5>Du hast {this.state.totalVoted} von 953 Sprüchen bewertet</h5>
          {jsx}
      </div>
    );
  }
};



