import React from 'react';
import TextField from 'react-md/lib/TextFields';
import Button from 'react-md/lib/Buttons/Button';
import { browserHistory } from 'react-router';

class MainLayout extends React.Component {

    constructor(props) {
        super(props);
        this.state = {name: ''}
    };

    onChange = (name) => {
        this.setState({name})
    };

    goToSayings = () => {
        browserHistory.push(`/sayings/${this.state.name}`)
    };

  render() {
      return (
      <div className="app">
          <div className="container">
            <h1>Sanfte Grüße!</h1>

              Bewertet wird von 1-3 Sternen, 1 für schwach, 3 für stark.<br />
              Das x ist nur für den Fall, dass du einen Spruch absolut unangemessen oder anstößig findest.<br />

              Dein Fortschritt wird gespeichert - gib deinen Namen ein (Schreibweise beachten) <br/>
              und du setzt da fort, wo du aufgehört hast.<br />

              Fröhliches Bewerten!<br/>
              Vielen Dank für deine Hilfe!<br />

              <TextField
                  id="floatingTitle"
                  label="zb. Bruce Banner"
                  placeholder="Namen eingeben"
                  customSize="title"
                  className="md-cell md-cell--bottom"
                  onChange={this.onChange}
              />
              <Button type="submit" onClick={this.goToSayings} raised primary label="Proceed"/>
          </div>
      </div>
    );
  }
};

export default MainLayout;



