import React from 'react';
import { Router, Route, browserHistory } from 'react-router';

// Layouts
import UserEntry from './components/userEntry-layout';

// Pages
import Sayings from './components/sayings';
import Admin from './components/admin';

export default (
  <Router history={browserHistory}>

      <Route path="/" component={UserEntry} />

      <Route path="sayings/:userName" component={Sayings} />

      <Route path="admin/unaccess" component={Admin} />

  </Router>
);



